import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  // BaseUrl:string="http://localhost:8080/api/";

  BaseUrl:string=environment.baseUrl;

  constructor(private http:HttpClient) { }

  login(obj){
    return (this.http.post(`${this.BaseUrl}login`,obj)); 
  }

  getAllEmployee(){
    return (this.http.get(`${this.BaseUrl}getAllEmployee`));
  }

  getEmployeeById(id){
    return (this.http.get(`${this.BaseUrl}getEmpById/${id}`));
  }

  getAllCountry(){
    return (this.http.get(`${this.BaseUrl}getAllCountry`));
  }

  saveEmployee(obj){
    return (this.http.post(`${this.BaseUrl}saveEmployee`,obj));
  }

  newRegistration(obj){
    return (this.http.post(`${this.BaseUrl}registerUser`,obj));
  }

  updateEmployee(obj){
    return (this.http.put(`${this.BaseUrl}updateEmployee`,obj,{responseType:'text'}));
  }

  DeleteRecord(id){
    return (this.http.delete(`${this.BaseUrl}deleteEmpById/${id}`,{responseType : 'text'}));
  }

  changeStatus(obj){
    return (this.http.put(`${this.BaseUrl}changeStatus`,obj,{responseType:'text'}));
  }
}
